5a5a5;\n\n  --logo-width: #{fluid(100px, 149px)};\n\n  @include breakpoint-xxs {\n    --logo-width: 30px;\n  }\n\n  --font-size-nav: #{fluid(20px, 28px)};\n  --font-size-text: #{fluid(22px, 34px)};\n\n  @include breakpoint-xxs {\n    --font-size-nav: 18px;\n    --font-size-text: 18px;\n  }\n\n  --font-size-h1: #{fluid(80px, 170px)};\n  --font-size-h2: #{fluid(36.5px, 90px)};\n  --font-size-skills-and-tools: #{fluid(20px, 24px)};\n  --font-size-skill-tag: #{fluid(16px, 18px)};\n  --font-size-visit-live-site: #{fluid(16px, 18px)};\n  --font-size-version: #{fluid(12px, 20px)};\n\n  @include breakpoint-xxs {\n    --font-size-h1: 60px;\n    --font-size-h2: 30px;\n    --font-size-skills-and-tools: 15px;\n    --font-size-skill-tag: 14px;\n    --font-size-visit-live-site: 14px;\n  }\n\n  --font-weight-thin: 300;\n  --font-weight-regular: 400;\n  --font-weight-medium: 510;\n  --font-weight-semibold: 550;\n\n  --flex-gap-nav: #{fluid(10px, 50px)};\n\n  --homepage-description-text-width: #{fluid(600px, 890px)};\n\n  --160px-to-80px: #{fluid(80px, 160px)};\n  --135px-to-70px: #{fluid(70px, 135px)};\n  --120px-to-60px: #{fluid(60px, 120px)};\n  --80px-to-40px: #{fluid(40px, 80px)};\n  --70px-to-35px: #{fluid(35px, 70px)};\n  --65px-to-45px: #{fluid(45px, 65px)};\n  --40px-to-25px: #{fluid(25px, 40px)};\n  --30px-to-15px: #{fluid(15px, 30px)};\n  --25px-to-10px: #{fluid(10px, 25px)};\n  --20px-to-10px: #{fluid(10px, 20px)};\n  --15px-to-10px: #{fluid(10px, 15px)};\n  --15px-to-8px: #{fluid(8px, 15px)};\n  --11px-to-5px: #{fluid(6px, 11px)};\n  --7px-to-3px: #{fluid(3px, 7px)};\n  --4px-to-2px: #{fluid(2px, 4px)};\n  --minus-10px-to-minus-5px: #{fluid(-5px, -10px)};\n\n  --profile-skill-list-text-width: #{fluid(500px, 600px)};\n\n  --single-project-text-width: #{fluid(560px, 860px)};\n  --single-project-skills-tag-padding1: #{fluid(5px, 15px)};\n  --single-project-skills-tag-padding2: #{fluid(10px, 25px)};\n\n  @include breakpoint-xxs {\n    --single-project-skills-tag-padding1: 3px;\n    --single-project-skills-tag-padding2: 6px;\n  }\n}\n\n/* GENERAL STYLES\n=============================================================================================== */\nhtml {\n  box-sizing: border-box;\n  min-width: 300px;\n  min-height: 100vh;\n}\n\nbody {\n  margin: 0;\n  padding: 1px 28px 0;\n\n  min-height: 100vh;\n\n  font-family: 'Inter', sans-serif;\n  letter-spacing: -0.3px;\n\n  @include breakpoint-xs {\n    padding: 0 15px;\n  }\n}\n\na {\n  text-decoration: none;\n  color: var(--primary-color);\n}\n\nh1,\nh2 {\n  margin: 0;\n}\n\nh1 {\n  max-width: 500px;\n  margin: 0 0 var(--70px-to-35px) var(--minus-10px-to-minus-5px);\n\n  font-size: var(--font-size-h1);\n  font-weight: var(--font-weight-regular);\n  line-height: 90%;\n\n  @include breakpoint-xxs {\n    margin: 0 0 30px -5px;\n  }\n}\n\nh2 {\n  margin: 0 0 20px -2px;\n\n  font-size: var(--font-size-h2);\n  font-weight: var(--font-weight-medium);\n  line-height: 100%;\n}\n\nh3,\np {\n  margin: 0;\n  padding: 0;\n\n  line-height: 145%;\n  font-size: var(--font-size-text);\n}\n\nh3 {\n  display: inline-block;\n  vertical-align: top;\n  margin: var(--20px-to-10px) 0 0;\n  font-weight: var(--font-weight-medium);\n}\n\np + p {\n  margin-top: 40px;\n}\n\nfigure {\n  margin: 0;\n  padding: 0;\n}\n\nimg,\nvideo {\n  display: block;\n  width: 100%;\n  max-width: 100%;\n}\n\n.hidden {\n  display: flex;\n  opacity: 0;\n  visibility: hidden;\n  pointer-events: none;\n  transition: opacity 0.2s, visibility 0.2s;\n}\n\n.hidden-xxs-half {\n  @include breakpoint-xxs-half {\n    display: none;\n  }\n}\n\n.visible {\n  opacity: 1;\n  visibility: visible;\n  display: flex;\n}\n\n.visible-xxs-half {\n  display: none;\n  @include breakpoint-xxs-half {\n    display: block;\n  }\n}\n\n/* LAZY LOADING\n=============================================================================================== */\n// Initial state of media-items with opacity set to 0\n.media {\n  opacity: 0;\n  transition: opacity 0.5s ease-out;\n}\n\n// Class to be added once the media is loaded\n.media-loaded {\n  opacity: 1;\n}\n\n/* TRANSITIONS\n=============================================================================================== */\n// Initial state of site title before transition\n.transition-in {\n  opacity: 0;\n  transform: translate3d(0, 10px, 0);\n}\n\n// Initial state of skills-and-tools div before transition\n.transition-in-opacity {\n  opacity: 0;\n}\n\n// Transition of site title\n.transition-visible {\n  opacity: 1;\n  transform: translate3d(0, 0, 0);\n  transition: opacity 0.5s ease-out 0.05s, transform 0.5s ease-out 0.05s;\n}\n\n// Transition of skills list on project pages\n.transition-visible-delayed {\n  opacity: 1;\n  transform: translate3d(0, 0, 0);\n  transition: opacity 0.5s ease-out 0.25s, transform 0.5s ease-out 0.25s;\n}\n\n// Transition of first media element on project pages\n.transition-visible-delayed-more {\n  opacity: 1;\n  transform: translate3d(0, 0, 0);\n  transition: opacity 0.5s ease-out 0.45s, transform 0.5s ease-out 0.45s;\n}\n\n/* LOGO + NAVIGATION\n=============================================================================================== */\nheader {\n  display: flex;\n  align-items: flex-end;\n  justify-content: space-between;\n\n  max-width: 1700px;\n  margin: 20px auto 0;\n\n  @include breakpoint-xs {\n    margin: 10px auto 0;\n  }\n\n  img {\n    max-width: var(--logo-width);\n    min-width: 80px;\n\n    @include breakpoint-xxs-half {\n      min-width: 31px;\n    }\n  }\n\n  nav {\n    font-weight: var(--font-weight-regular);\n\n    ul {\n      display: flex;\n      gap: var(--flex-gap-nav);\n\n      margin: 0 -15px -5px 0;\n      padding: 0 0 0 30px;\n\n      list-style: none;\n      font-size: var(--font-size-nav);\n\n      @include breakpoint-xxs {\n        margin: 0 -9px -2px 0;\n        padding: 0 0 0 10px;\n      }\n\n      li a {\n        padding: 8px 15px;\n        border-radius: 8px;\n\n        @include breakpoint-xxs {\n          padding: 4px 9px;\n        }\n\n        &:active {\n          background-color: rgba(0, 0, 0, 0.05);\n        }\n      }\n    }\n  }\n}\n\n/* GENERAL STYLES FOR #CONTENT\n=============================================================================================== */\n#content {\n  display: flex;\n  flex-direction: column;\n\n  max-width: 1700px;\n  margin: var(--80px-to-40px) auto 0;\n  padding: 0;\n\n  .homepage,\n  .profile-page {\n    padding: 0 0 var(--160px-to-80px);\n  }\n}\n\n/* PROJECT LOADING ANIMATION\n=============================================================================================== */\n#loading-wrapper {\n  position: absolute;\n  top: 40%;\n  left: 50%;\n  transform: translateX(-50%);\n\n  align-items: center;\n  flex-direction: column;\n  gap: 15px;\n\n  height: 100px;\n\n  span {\n    font-size: var(--font-size-skill-tag);\n    font-weight: var(--font-weight-thin);\n    color: var(--primary-color);\n  }\n}\n\n.circle {\n  display: inline-block;\n  background-color: var(--primary-color);\n  height: 15px;\n  width: 15px;\n  border-radius: 25px;\n}\n\n#ball-1 {\n  animation-name: bounce;\n  animation-delay: 0s;\n  animation-duration: 1.4s;\n  animation-iteration-count: infinite;\n}\n#ball-3 {\n  animation-name: bounce;\n  animation-delay: 0.1s;\n  animation-duration: 1.4s;\n  animation-iteration-count: infinite;\n}\n\n@keyframes bounce {\n  0% {\n    transform: translateY(0);\n  }\n  40% {\n    transform: translateY(8px);\n  }\n  60% {\n    transform: translateY(-11px);\n  }\n  80% {\n    transform: translateY(0);\n  }\n}\n\n/* Footer\n=============================================================================================== */\nfooter {\n  display: flex;\n  align-items: baseline;\n  justify-content: space-between;\n\n  max-width: 1700px;\n  margin: 0 auto;\n  padding: var(--40px-to-25px) 0 var(--65px-to-45px);\n\n  border-top: 2px solid var(--primary-color);\n\n  a {\n    padding: 8px 15px;\n    border-radius: 8px;\n    margin-right: -15px;\n\n    @include breakpoint-xxs {\n      padding: 4px 9px;\n      margin-right: -9px;\n    }\n\n    &:active {\n      background-color: rgba(0, 0, 0, 0.05);\n    }\n  }\n\n  @include breakpoint-xxs {\n    padding: 15px 0 30px;\n  }\n\n  p {\n    margin: 0;\n  }\n}\n\nbody.not-found {\n  footer {\n    display: none;\n  }\n}\n\n/* 404 page\n=============================================================================================== */\n.page-not-found {\n  h1 {\n    margin: 0 0 var(--70px-to-35px) 0;\n  }\n  .description {\n    max-width: var(--homepage-description-text-width);\n\n    span {\n      display: inline-flex;\n    }\n  }\n\n  .arrow-right {\n    display: inline;\n    width: var(--40px-to-25px);\n    margin: 0 0 -3px 10px;\n    transform: rotate(180deg);\n\n    @include breakpoint-xxs {\n      width: 20px;\n    }\n  }\n}\n\n/* Prevent flashes of unstyled content\n=============================================================================================== */\n// Try to hide the DOM as long as possible via inline styles\n// to prevent Flashes of unstyled text (FOUT). Then unhide the\n// DOM at the end of the CSS document:\nhtml {\n  visibility: visible;\n  opacity: 1;\n}\n","/* BREAKPOINTS\n=============================================================================================== */\n@mixin breakpoint-lg {\n  @media (max-width: 1424px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-MIN-lg {\n  @media (min-width: 1200px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-md {\n  @media (max-width: 1199px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-MIN-md {\n  @media (min-width: 992px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-sm {\n  @media (max-width: 991px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-MIN-sm {\n  @media (min-width: 768px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-xs {\n  @media (max-width: 767px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-MIN-xs {\n  @media (min-width: 601px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-xs-half {\n  @media (max-width: 600px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-MIN-xs-half {\n  @media (min-width: 421px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-xxs {\n  @media (max-width: 420px) {\n    @content;\n  }\n}\n\n@mixin breakpoint-xxs-half {\n  @media (max-width: 370px) {\n    @content;\n  }\n}\n","@import 'general-styles';\n\n/* PROFILE PAGE\n=============================================================================================== */\n.profile-page {\n  .text {\n    p {\n      max-width: var(--homepage-description-text-width);\n    }\n  }\n\n  > .text:first-of-type {\n    margin: 0 0 var(--120px-to-60px);\n  }\n\n  .two-column {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    gap: 15px;\n\n    p {\n      max-width: var(--profile-skill-list-text-width);\n      font-weight: var(--font-weight-thin);\n    }\n  }\n}\n"],"sourceRoot":""}]);
